.sidebar {
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: column;
}

.sidebar a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.493);
  font: 200 1rem "Roboto";
  padding: 2rem 1rem;
  transition: all 0.5s;
}

.sidebar a:hover {
  color: tomato;
}

.sidebar a > P {
  display: flex;
  align-items: center;
}
.sidebar a > p > svg {
  margin-right: 0.5rem;
}