.newProductContainer {
  width: 100%;
  box-sizing: border-box;
  background-color: rgb(221, 221, 221);
  border-left: 1px solid rgba(0, 0, 0, 0.158);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.newProductContainer h1 {
  color: rgba(0, 0, 0, 0.733);
  font: 300 2rem "Roboto";
  text-align: center;
}

.newProductForm {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 3vmax;
  justify-content: space-evenly;
  min-height: 70%;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.267);
}

.newProductForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}
.newProductForm > div > input,
.newProductForm > div > select,
.newProductForm > div > textarea {
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  margin: 1rem 0;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.267);
  border-radius: 4px;
  font: 300 0.9vmax cursive;
  outline: none;
}

.newProductForm > div > svg {
  position: absolute;
  transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: rgba(0, 0, 0, 0.623);
}

#newProductFormFile > input {
  display: flex;
  padding: 0%;
}

#newProductFormFile > input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 5vh;
  border: none;
  margin: 0%;
  font: 400 0.8vmax cursive;
  transition: all 0.5s;
  padding: 0 1vmax;
  color: rgba(0, 0, 0, 0.623);
  background-color: rgb(255, 255, 255);
}

#newProductFormFile > input::file-selector-button:hover {
  background-color: rgb(235, 235, 235);
}

#newProductFormImage {
  width: 100%
}

#newProductFormImage > img {
  width: 5vmax;
  margin: 0.5vmax;
}

#newProductBtn {
  border: none;
  background-color: tomato;
  color: white;
  font: 300 0.9vmax "Roboto";
  width: 30%;
  margin: 0.5rem auto;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

#newProductBtn:hover {
  background-color: rgb(179, 66, 46);
}

@media screen and (max-width: 600px) {
  .newProductContainer {
    background-color: rgb(255, 255, 255);
  }

  .newProductForm > div > input,
  .newProductForm > div > select,
  .newProductForm > div > textarea {
    margin: 0.5rem 0;
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .newProductForm > div > svg {
    font-size: 2.8vmax;
  }

  #newProductFormFile > img {
    width: 8vmax;
    border-radius: 100%;
  }

  #newProductFormFile > input::file-selector-button {
    height: 7vh;
    font: 400 1.8vmax cursive;
  }

  #newProductBtn {
    font: 300 1.9vmax "Roboto";
    padding: 1.8vmax;
  }
}
