.productsHeading {
    margin: 2vmax auto;
    width: 15vw;
    border-bottom: 1px solid rgba(0, 0, 0, 0.171);
    padding: 2vmax;
    color: rgba(0, 0, 0, 0.678);
    font: 500 1.5vamx "Roboto";
    text-align: center;
}

.products {
    display: flex;
    flex-wrap: wrap;
    padding: 0 5vmax;
    justify-content: center;
    min-height: 40vh;
}

/* Paggination */
.paginationBox {
    display: flex;
    justify-content: center;
    margin: 6vmax;
}

.pagination {
    display: flex;
    justify-content: center;
    padding: 0;
}

.page-item {
    list-style: none;
    background-color: rgb(255, 255, 255);
    border: 1px solid rgba(0, 0, 0, 0.171);
    padding: 1vmax 1.5vmax;
    transition: all 0.5s;
    cursor: pointer;
}

.page-item:last-child {
    border-radius: 0 5px 5px 0;
}

.page-item:first-child {
    border-radius: 5px 0 0 5px;
}

.page-link {
    text-decoration: none;
    font-size: 300 0.7vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
}

.page-item:hover {
    background-color: rgb(230, 230, 230);
}

.pageItemActive {
    background-color: tomato;
}

.pageLinkActive {
    color: white;
}

/* Filter */
.filterBox {
    width: 8vmax;
    position: absolute;
    top: 10vmax;
    left: 4vmax;
}

.categoryBox {
    padding: 0;
}

.category-link {
    list-style: none;
    color: rgba(0, 0, 0, 0.65);
    font: 300 0.8vmax "Roboto";
    margin: 0.4vmax;
    cursor: pointer;
    transition: all 0.5s;
}

.category-link:hover {
    color: tomato;
}

.filterBox > fieldset {
    border: 1px solid rgba(0, 0, 0, 0.397);
}

@media screen and (max-width: 600px) {
    .filterBox {
        width: 19vmax;
        position: static;
        margin: auto;
    }

    .page-link {
        font: 300 1.8vmax "Roboto";
    }

    .category-link {
        font: 400 1.8vmax 'Roboto';
    }
}
