.updatePasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rbg(231, 231, 231);
  }
  
  .updatePasswordBox {
    background-color: white;
    width: 25vw;
    height: 70vh;
    box-sizing: border-box;
    overflow: hidden;
  }
  
  .updatePasswordBox > h2 {
      color: rgba(0, 0, 0, 0.651);
      text-align: center;
      font: 500 1.3vmax "Roboto";
      padding: 1.3vmax;
      border-bottom: 1px solid rgba(0, 0, 0, 0.240);
      width: 50%;
      margin: auto;
  }
  
  .updatePasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    height: 70%;
    justify-content: space-evenly;
    transition: all 0.5s;
  }
  
  .updatePasswordForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  .updatePasswordForm > div > input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    font: 400 0.9vmax cursive;
    border: 1px solid rgba(0, 0, 0, 0.278);
    border-radius: 4px;
    outline: none;
  }
  
  .updatePasswordForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.678);
  }
  
  .updatePasswordBtn {
    border: none;
    background-color: tomato;
    color: white;
    width: 100%;
    font: 400 0.9vmax "Roboto";
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.278);
  }
  
  .updatePasswordBtn:hover {
    background-color: rgb(146, 66, 44);
  }
  
  @media screen and (max-width: 600px) {
    .updatePasswordContainer {
      background-color: white;
    }
  
    .updatePasswordBox {
      width: 100vw;
      height: 95vh;
    }
  
    .updatePasswordForm {
      padding: 5vmax;
    }
  
    .updatePasswordForm > div > input {
      padding: 2vmax 5vmax;
      font: 300 1.7vmax cursive;
    }
  
    .updatePasswordForm > div > svg {
      font-size: 2.8vmax;
    }
    
    .updatePasswordBtn {
      font: 400 1.9vmax "Roboto";
      padding: 1.8vmax;
    }
  }
  